import React, { useState } from 'react';
import UserProfileModal from './UserProfileModal';
import WorkloadBar from './WorkloadBar';
import authService from '../../services/authServices';
import '../../assets/styles/compenents/admin/UserProfileModal.scss';
import '../../assets/styles/compenents/admin/WorkloadBar.scss';
import '../../assets/styles/compenents/admin/AdminPermissions.scss';

const UsersTab = ({ projectUsers, projectTasks, onAddUser, onUserUpdated, selectedProject, currentUserRole }) => {
    const [selectedUser, setSelectedUser] = useState(null);
    const [showUserProfile, setShowUserProfile] = useState(false);

    const getRoleDisplayName = (role) => {
        if (!role) return 'Non d√©fini';
        
        const roleStr = typeof role === 'string' ? role : String(role);
        
        switch (roleStr) {
            case 'Responsable de Projet':
            case 'responsable_projet':
                return 'Responsable de Projet';
            case 'Manager':
            case 'manager':
                return 'Manager';
            case 'Collaborateur':
            case 'collaborateur':
                return 'Collaborateur';
            default:
                return roleStr;
        }
    };

    const getWorkloadForUser = (userId) => {
        return projectTasks
            .filter(task => 
                task.assignedTo && 
                (typeof task.assignedTo === 'object' ? task.assignedTo.id === userId : task.assignedTo === userId)
            )
            .reduce((total, task) => total + (task.estimatedHours || 1), 0);
    };

    return (
        <div className="users-tab">
            <div className="tab-header">
                <h3>Gestion des Utilisateurs</h3>
                <div className="tab-actions">
                    {authService.canManageUsers(currentUserRole) && (
                        <button 
                            className="btn-primary"
                            onClick={onAddUser}
                        >
                            + Ajouter un utilisateur
                        </button>
                    )}
                    {authService.isManager(currentUserRole) && (
                        <div className="manager-info">
                            <span className="info-text">Mode consultation uniquement</span>
                        </div>
                    )}
                </div>
            </div>
            
            <div className="project-users-detailed">
                <h4>Utilisateurs du projet</h4>
                {projectUsers.length === 0 ? (
                    <p>Aucun utilisateur trouv√© dans ce projet.</p>
                ) : (
                    <div className="users-detailed-list">
                                                            {projectUsers.map(user => (
                                        <div 
                                            key={user.id} 
                                            className={`user-detailed-item ${authService.canManageUsers(currentUserRole) ? 'clickable' : 'readonly'}`}
                                            onClick={authService.canManageUsers(currentUserRole) ? () => {
                                                setSelectedUser(user);
                                                setShowUserProfile(true);
                                            } : undefined}
                                            title={authService.canManageUsers(currentUserRole) ? 
                                                "Cliquer pour voir le profil d√©taill√©" : 
                                                "Mode consultation uniquement - Modification non autoris√©e"
                                            }
                                        >
                                <div className="user-header">
                                    <div className="user-info">
                                        <strong>{user.firstname} {user.lastname}</strong>
                                        <span className="user-email">{user.email}</span>
                                    </div>
                                    <span className={`user-role role-${user.role}`}>
                                        {getRoleDisplayName(user.role)}
                                    </span>
                                </div>
                                
                                <div className="user-details">
                                    <div className="user-section">
                                        <h5>T√¢ches assign√©es</h5>
                                        <div className="user-tasks">
                                            {projectTasks.filter(task => 
                                                task.assignedTo && 
                                                (typeof task.assignedTo === 'object' ? 
                                                    task.assignedTo.id === user.id : 
                                                    task.assignedTo === user.id)
                                            ).length === 0 ? (
                                                <p className="no-tasks">Aucune t√¢che assign√©e</p>
                                            ) : (
                                                projectTasks.filter(task => 
                                                    task.assignedTo && 
                                                    (typeof task.assignedTo === 'object' ? 
                                                        task.assignedTo.id === user.id : 
                                                        task.assignedTo === user.id)
                                                ).map(task => (
                                                    <div key={task.id} className="user-task-item">
                                                        <span className="task-title">{task.title}</span>
                                                        <span className={`task-priority priority-${task.priority}`}>
                                                            {task.priority}
                                                        </span>
                                                        <span className="task-status">{task.status}</span>
                                                    </div>
                                                ))
                                            )}
                                        </div>
                                    </div>
                                    
                                    <div className="user-section">
                                        <h5>Comp√©tences</h5>
                                        <div className="user-skills">
                                            {user.skills && user.skills.length > 0 ? (
                                                user.skills.map(skill => (
                                                    <span key={skill.id} className="skill-tag">
                                                        {skill.name}
                                                    </span>
                                                ))
                                            ) : (
                                                <p className="no-skills">Aucune comp√©tence d√©finie</p>
                                            )}
                                        </div>
                                    </div>
                                    
                                    <div className="user-section">
                                        <h5>Charge de travail</h5>
                                        <WorkloadBar 
                                            currentHours={getWorkloadForUser(user.id)}
                                            maxHours={user.maxWorkloadHours || 40}
                                            user={user}
                                        />
                                    </div>
                                    
                                    <div className="user-section">
                                        <h5>Statistiques</h5>
                                        <div className="user-stats">
                                            <div className="stat-item">
                                                <span className="stat-number">
                                                    {projectTasks.filter(task => 
                                                        task.assignedTo && 
                                                        (typeof task.assignedTo === 'object' ? 
                                                            task.assignedTo.id === user.id : 
                                                            task.assignedTo === user.id)
                                                    ).length}
                                                </span>
                                                <span className="stat-label">T√¢ches</span>
                                            </div>
                                            <div className="stat-item">
                                                <span className="stat-number">
                                                    {user.skills ? user.skills.length : 0}
                                                </span>
                                                <span className="stat-label">Comp√©tences</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>

            {showUserProfile && selectedUser && selectedProject && (
                <UserProfileModal
                    isOpen={showUserProfile}
                    onClose={() => {
                        setShowUserProfile(false);
                        setSelectedUser(null);
                    }}
                    user={selectedUser}
                    projectId={selectedProject.id}
                    projectUsers={projectUsers}
                    projectTasks={projectTasks}
                    onUserUpdated={onUserUpdated}
                />
            )}
        </div>
    );
};

export default UsersTab;
